/****** Import external files ******/

@import "../sassImport/reset.scss";
@import "../sassImport/animate.scss";
@import "../sassImport/utilities/_all";
@import "https://fonts.googleapis.com/css?family=IM+Fell+English:400,400i";


/****** Set color variable ******/

$primary: rgb(67, 52, 82);
$bouton: rgb(67, 52, 82);


/****** Calculated variables ******/

$primary-dark: darken($primary, 20%);
$primary-light: lighten($primary, 20%);

$primary-opposite: hsl((hue($primary)+180), saturation($primary), lightness($primary));
$primary-opposite-dark: darken($primary-opposite, 20%);
$primary-opposite-light: lighten($primary-opposite, 20%);

$primary-invert: findColorInvert($primary);
$primary-opposite-invert: findColorInvert($primary-opposite);

/****** import bulma.css modules ******/

@import "../sassImport/elements/_all";

/****** mixin ******/


@mixin flexInRow {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
}

@mixin flexInColumn {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
}

@mixin gridCenter {
    display: grid;
    align-items: center;
    justify-items: center;
}

@mixin fullBackground($backgroundImage) {
    background: no-repeat fixed center;
    background-image: url($backgroundImage);
    background-size: cover;
}

@mixin shadowed {
    box-shadow: 0 3px 6px rgba(0,0,0,0.23);
}

@mixin shadowedHoverable {
    box-shadow: 0 3px 6px rgba(0,0,0,0.23);
    &:hover {
        box-shadow: 0 14px 28px rgba(0,0,0,0.25);
    }
}


/*--------------------------------------------------------------------------*/
/*--------------------------------------------------------------------------*/


.marketPage {
    background-color: $primary;
    font-family: 'IM Fell English', serif;
    min-height: 100vh;
}

.enseigne {
    @include flexInRow();
    color: white;
    padding: 1rem;
}

.message {
    background-color: wheat;
    border: solid black 1px;
    margin: 1rem;
    padding: 1rem;
    font-size: 1.25rem;
}

h1 {
    font-size: 2rem;  
}

h2 {
    font-size: 1.5rem;
}

img {
    width: 50px;
}
.market, .team{
    background-color: wheat;
    border: solid black 1px;
    margin: 1rem;
    padding: 1rem;
}
.onSaleCategory, .teamCategory {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    overflow-x: auto;
}

.fighterOnSale, .fighterInTeam, .itemsOnSale, .weaponsOnSale, .armorsOnSale, .weaponsInTeam, .armorsInTeam, .itemsInTeam {
    background-color: white;
    border: solid black 1px;
    margin: 1rem;
    padding: 1rem;
    width: 12rem;
    min-width: 12rem;
}